*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'Khula', sans-serif;
}
body{
	background-color: #fcfcfc;
	position: relative;
}

main > div.shown{
	background-color: #fff;
	border: 2px solid #dfdfdf;
	border-radius: 8px;
	width: 90%;
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-auto-rows: 22px;
	min-height: 206px;
	padding: .5em .5em 2px;
	max-width: 416px;
	-webkit-transition: border-color .8s;
	transition: border-color .8s;
}
main > div.shown:hover,
main > div.shown:focus{
	outline: none;
	border-color: #4a4a4a;
}
main > div.shown:last-of-type{
	margin-bottom: 0;
}
div.shown img{
	grid-column-end: 2;
	border-radius: 50%;
}
.shown p.name{
	line-height: 24px;
	font-weight: 700;
	grid-column-end: 3;
	text-align: right;
	grid-row-end: 2;
	font-size: 21px;
	color: #404a51;
}
.shown p{
	font-weight: 400;
	font-size: 17px;
	color: #555;
}
.shown .user,
.shown .email,
.shown .city{
	grid-column-start: 1;
	grid-column-end: 3;
	justify-self: center;
}
.shown .user{
	grid-row-start: 8;
}
.shown .email{
	grid-row-start: 9;
}
.shown .city{
	grid-row-start: 7;
  text-transform: capitalize;
}
.shown p.email:hover{
	text-decoration: underline;
	color: blue;
	cursor: pointer;
}
@media screen and (min-width: 1292px){
	main > div.shown{
		width: 400px;
	}
}

input{
	height: 44px;
	background-color: #fff;
	width: 88%;
	border: 2px solid #dfdfdf;
	border-radius: 8px;
	padding-left: 14px;
	font-size: 16px;
	color: #555;
	max-width: 416px;
	-webkit-transition: border-color .8s;
	transition: border-color .8s;
}
input:focus{
	outline: none;
	border-color: #4a4a4a;
	color: #555;
}
input::-webkit-input-placeholder {
	color: #555;
}
input::-moz-placeholder {
	color: #555;
}
@media screen and (min-width: 1024px){
	input{
		width: 35%;
	}
}

label{
	color: #4a4a4a;
	display: block;
	text-align: center;
}
.h1_input div{
	margin-top: 1em;
}
select{
	border: 2px solid #dfdfdf;
	border-radius: 4px;
	color: #4a4a4a;
	-webkit-transition: border-color .8s;
	transition: border-color .8s;
}
select:focus{
	outline: none;
	border-color: #4a4a4a;
}
@media screen and (min-width: 1024px){
	.h1_input div{
		margin-top: 0;
	}
	label{
		margin-right: 9px;
		display: inline;
	}
}

.hidden{
	display: -webkit-flex;
	display: flex;
	position: absolute;
	z-index: 44;
	background-color: #fff;
	border: 2px solid #dfdfdf;
	border-radius: 8px;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	width: 90%;
	padding: 3em 1.5em;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
	left: 50%;
	max-width: 456px;
}
.hidden button{
	position: absolute;
	background-color: #fff;
	border: 2px solid transparent;
	font-size: 30px;
	width: 15px;
	cursor: pointer;
	box-sizing: content-box;
}
.hidden button:focus{
	outline: none;
	border: 2px solid #4a4a4a;
}
.hidden button.close{
	text-transform: uppercase;
	right: 4px;
	top: 4px;
	font-size: 20px;
}
.hidden button.prev{
	left: 5px;
	height: 40px;
	top: 19%;
}
.hidden button.next{
	right: 5px;
	height: 40px;
	top: 19%;
}
.hidden img{
	width: 170px;
	height: 170px;
	border-radius: 50%;
	margin-bottom: 1em;
}
.hidden div{
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	width: 100%;
}
.hidden div.top-part{
	padding-bottom: 1.5em;
	border-bottom: 1px solid #dadada;
}
.hidden div.bottom-part{
	padding-top: 1.5em;
	line-height: 2.1;
}
.hidden p{
	font-weight: 400;
	font-size: 17px;
	color: #555;
}
.hidden p.name{
	line-height: 24px;
	font-weight: 700;
	margin-bottom: 0.25em;
	color: #404a51;
	font-size: 21px;
}
.hidden div p.email:hover{
	text-decoration: underline;
	color: blue;
	cursor: pointer;
}
.hidden .city,
.hidden .addy{
	text-transform: capitalize;
}
.hidden .addy{
  text-align: center;
}

div.overlay{
	position: absolute;
	min-height: 100vh;
	width: 100%;
	height: 100%;
	opacity: .5;
	background-color: #000;
	z-index: 35;
}
main{
	padding-bottom: 3em;
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(272px, 1fr));
	grid-gap: 40px 33px;
	justify-items: center;
}
h1{
	color: #4a4a4a;
	font-weight: 600;
	text-transform: uppercase;
	font-size: 21px;
	letter-spacing: 1px;
	text-align: center;
	margin: 0 0.25em 1em;
}
div.h1_input{
	padding-top: 2em;
	margin-bottom: 2em;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
}
@media screen and (min-width: 490px) and (max-width: 767px), screen and (min-width: 899px) and (max-width: 1023px), screen and (min-width: 1292px){
	main{
		grid-template-columns: repeat(auto-fill, minmax(416px, 1fr));
	}
}
@media screen and (min-width: 768px) and (max-width: 898px), screen and (min-width: 1024px) and (max-width: 1291px){
	main{
		grid-template-columns: repeat(2, minmax(272px, 1fr));
	}
}
@media screen and (min-width: 899px){
	main{
		grid-template-columns: repeat(2, minmax(416px, 1fr));
	}
}
@media screen and (min-width: 1024px){
	div.h1_input{
		width: 95%;
		padding-top: 4em;
		margin: 0 auto 3em;
		-webkit-flex-direction: row;
		        flex-direction: row;
		-webkit-justify-content: space-between;
		        justify-content: space-between;
	}
	h1{
		margin: 0;
	}
	main{
		grid-template-columns: repeat(3, minmax(272px, 1fr));
	}
}
@media screen and (min-width: 1292px){
	main{
		width: 95%;
		margin: 0 auto;
	}
}

